@import '../../../../../styles/templarios.abstracts';

:host {
  position: relative;
  display: flex;
  width: 100%;
}

.tp-news-hero {
  &__container {
    display: flex;
    gap: 16px;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    padding: 24px;
    box-sizing: border-box;
    background: linear-gradient(
      180deg,
      rgba(0, 0, 0, 0.1) 50%,
      rgba(0, 0, 0, 0.5) 100%
    );
  }

  &__left {
    flex: 2;
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    height: 100%;
    gap: 24px;

    ::slotted([slot='category']) {
      --padding-start: 24px !important;
      --padding-end: 24px !important;
      --padding-top: 2px !important;
      --padding-bottom: 2px !important;
    }

    ::slotted([slot='title']) {
      h1 {
        font-size: 48px;
        line-height: 48px;
        font-weight: 600;

        @include breakpoint-down('lg') {
          font-size: 32px !important;
          line-height: 32px !important;
        }
      }
    }
  }

  &__right {
    flex: 0 1 240px;
    display: flex;
    justify-content: center;
    align-items: center;

    @include breakpoint-down('md') {
      display: none;
    }
  }
}
