import { Component, Host, h } from '@stencil/core';

@Component({
  tag: 'tp-elem-news-hero',
  styleUrl: 'news-hero.scss',
  scoped: true,
})
export class TpElemNewsHero {
  render() {
    return (
      <Host class="tp-news-hero">
        <div class="tp-news-hero__container">
          <div class="tp-news-hero__left">
            <slot name="category"></slot>
            <slot name="title"></slot>
            <slot name="date-time"></slot>
          </div>

          <div class="tp-news-hero__right">
            <slot name="watermark"></slot>
          </div>
        </div>
      </Host>
    );
  }
}
